
<template>
<view>
<mescroll-uni @init="mescrollInit" :up="upOption" :down="downOption" @down="downCallback" @up="upCallback">
<view class="content">
<view class="container" :style='{"padding":"0","flexWrap":"wrap","background":"#EFEFEF","display":"flex","width":"100%","position":"relative","height":"100%"}'>
<swiper :style='{"width":"100%","margin":"10px","height":"190px"}' class="swiper" :indicator-dots='false' :autoplay='false' :circular='false' indicator-active-color='#000000' indicator-color='#00000030' :duration='500' :interval='5000' :vertical='false'>
<swiper-item :style='{"width":"100%","borderRadius":"10px","background":"none","height":"180px"}' v-for="(swiper,index) in swiperList" :key="index" @tap="previewImgTap(swiper)">
<img :style='{"width":"100%","objectFit":"cover","borderRadius":"10px","display":"block","height":"180px"}' mode="aspectFill" v-if="swiper.substring(0,4)=='http'" :src="swiper"></img>
<img :style='{"width":"100%","objectFit":"cover","borderRadius":"10px","display":"block","height":"180px"}' mode="aspectFill" v-else :src="baseUrl+swiper"></img>
</swiper-item>
</swiper>
<view :style='{"width":"100%","padding":"12px 12px 12px 12px","flexWrap":"wrap","background":"#EFEFEF","display":"flex","height":"auto"}' class="detail-content">

<view class="detail-list-item" :style='{"margin":"0 0 12px 0","borderColor":"#F8678C","borderWidth":"0 0 1px 0","display":"flex","width":"100%","borderStyle":"solid","height":"auto"}'>
<view class="lable" :style='{"width":"80px","padding":"0 10px 0 0","lineHeight":"40px","fontSize":"14px","color":"#333","textAlign":"right"}'>商家名称：</view>
<view  class="text" :style='{"padding":"0 0 0 0","margin":"0 0 0 0","lineHeight":"40px","fontSize":"14px","color":"#000"}'>{{detail.shangjiamingcheng}}</view>
</view>
<view class="detail-list-item" :style='{"margin":"0 0 12px 0","borderColor":"#F8678C","borderWidth":"0 0 1px 0","display":"flex","width":"100%","borderStyle":"solid","height":"auto"}'>
<view class="lable" :style='{"width":"80px","padding":"0 10px 0 0","lineHeight":"40px","fontSize":"14px","color":"#333","textAlign":"right"}'>联系电话：</view>
<view  class="text" :style='{"padding":"0 0 0 0","margin":"0 0 0 0","lineHeight":"40px","fontSize":"14px","color":"#000"}'>{{detail.lianxidianhua}}</view>
</view>
<view class="detail-list-item" :style='{"margin":"0 0 12px 0","borderColor":"#F8678C","borderWidth":"0 0 1px 0","display":"flex","width":"100%","borderStyle":"solid","height":"auto"}'>
<view class="lable" :style='{"width":"80px","padding":"0 10px 0 0","lineHeight":"40px","fontSize":"14px","color":"#333","textAlign":"right"}'>联系地址：</view>
<view  class="text" :style='{"padding":"0 0 0 0","margin":"0 0 0 0","lineHeight":"40px","fontSize":"14px","color":"#000"}'>{{detail.lianxidizhi}}</view>
</view>



<view class="detail-list-item" :style='{"margin":"0 0 12px 0","borderColor":"#F8678C","borderWidth":"0 0 1px 0","display":"flex","width":"100%","borderStyle":"solid","height":"auto"}'>
<view class="lable" :style='{"width":"80px","padding":"0 10px 0 0","lineHeight":"40px","fontSize":"14px","color":"#333","textAlign":"right"}'>店铺资质</view>
<view class="text" :style='{"padding":"0 0 0 0","margin":"0 0 0 0","lineHeight":"40px","fontSize":"14px","color":"#000"}'>{{detail.dianpuzizhi}}</view>
</view>

<view v-if="userid" class="detail-list-item" :style='{"margin":"0 0 12px 0","borderColor":"#F8678C","borderWidth":"0 0 1px 0","display":"flex","width":"100%","borderStyle":"solid","height":"auto"}'>
<view class="lable" :style='{"width":"80px","padding":"0 10px 0 0","lineHeight":"40px","fontSize":"14px","color":"#333","textAlign":"right"}'>审核状态：</view>
<view v-if="detail.sfsh=='是'" class="text" :style='{"padding":"0 0 0 0","margin":"0 0 0 0","lineHeight":"40px","fontSize":"14px","color":"#000"}'>通过</view>
<view v-if="detail.sfsh=='否'" class="text" :style='{"padding":"0 0 0 0","margin":"0 0 0 0","lineHeight":"40px","fontSize":"14px","color":"#000"}'>不通过</view>
<view v-if="detail.sfsh=='待审核'|| !detail.sfsh" class="text" :style='{"padding":"0 0 0 0","margin":"0 0 0 0","lineHeight":"40px","fontSize":"14px","color":"#000"}'>待审核</view>
</view>
<view v-if="userid" class="detail-list-item" :style='{"margin":"0 0 12px 0","borderColor":"#F8678C","borderWidth":"0 0 1px 0","display":"flex","width":"100%","borderStyle":"solid","height":"auto"}'>
<view class="lable" :style='{"width":"80px","padding":"0 10px 0 0","lineHeight":"40px","fontSize":"14px","color":"#333","textAlign":"right"}'>审核回复</view>
<view class="text" :style='{"padding":"0 0 0 0","margin":"0 0 0 0","lineHeight":"40px","fontSize":"14px","color":"#000"}'>{{detail.shhf}}</view>
</view>




<view class="bottom-content bg-white tabbar border shop" :style='{"padding":"5px 12px 5px 12px","flexWrap":"wrap","background":"none","display":"flex","width":"100%","height":"auto","order":"7"}'>




<button :style='{"border":"0","padding":"0 10px","margin":"5px","color":"#fff","display":"flex","justifyContent":"center","outline":"none","borderRadius":"25px","background":"#000000","width":"auto","fontSize":"14px","lineHeight":"40px","height":"40px"}' v-if="userid&&isAuth('shangjia','审核')"  @tap="onSHTap">审核</button>
<button :style='{"border":"0","padding":"0 10px","margin":"5px","color":"#fff","display":"flex","justifyContent":"center","outline":"none","borderRadius":"25px","background":"#000000","width":"auto","fontSize":"14px","lineHeight":"40px","height":"40px"}' v-if="!userid&&isAuthFront('shangjia','审核')"  @tap="onSHTap">审核</button>
</view>
</view>



<!-- 确认完成弹窗 -->
<uni-popup class="popup-content" ref="popup" type="center">
<form class="popup-form" style="background: #fff;width: 300px;display: block;border-radius: 10px;padding: 20px;text-align: center;">
<view class=" margin-top">
<picker @change="sfshChange" :value="sfshIndex" :range="sfshOptions">
<view class="uni-input">{{detail.sfsh?detail.sfsh:'选择审核结果'}}</view>
</picker>
</view>
<view class="">
<!-- <view class="title">审核回复</view> -->
<textarea style="width: 200px;" v-model="detail.shhf" placeholder="审核回复"></textarea>
<!-- <input v-model="douyinId" placeholder="审核回复" name="input"></input> -->
</view>
<view class="btn-content">
<button style="margin: 0 10px" @tap="onCloseWinTap" class="cu-btn bg-cyan">取消</button>
<button style="margin: 0 10px" @tap="onFinishTap" class="cu-btn bg-red">确认</button>
</view>
</form>
</uni-popup>
</view>
</view>
</mescroll-uni>

</view>

</template>

<script>
export default {
data() {
return {
username:"",
btnColor: ['#409eff','#67c23a','#909399','#e6a23c','#f56c6c','#356c6c','#351c6c','#f093a9','#a7c23a','#104eff','#10441f','#a21233','#503319'],
id: '',
userid: '',
detail: {},
swiperList: [],
commentList: [],
mescroll: null, //mescroll实例对象
downOption: {
auto: false //是否在初始化后,自动执行下拉回调callback; 默认true
},
upOption: {
noMoreSize: 3, //如果列表已无数据,可设置列表的总数量要大于半页才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看; 默认5
textNoMore: '~ 没有更多了 ~',
},
hasNext: true,
user: null,
sfshIndex: -1,
sfshOptions: ['通过','不通过', '待审核'],
freeChapterList: [],
count: 0,
timer: null,

}
},
components: {
},
computed: {
baseUrl() {
return this.$base.url;
},
},
async onLoad(options) {
this.id = options.id;
if(options.userid) {
this.userid = options.userid;
}
// 渲染数据
this.init();
},
onUnload() {
if(this.timer) {
clearInterval(this.timer);
}
},
async onShow(options) {
let table = uni.getStorageSync("nowTable");
// 获取用户信息
let res = await this.$api.session(table);
this.user = res.data;
this.btnColor = this.btnColor.sort(()=> {
return (0.5-Math.random());
});
let crossCleanType = uni.getStorageSync('crossCleanType')
if(crossCleanType) {
uni.removeStorageSync('crossCleanType')
res = await this.$api.info('shangjia', this.id);
this.detail = res.data;
}
},
destroyed: function() {
//window.clearInterval(this.inter);
},
methods: {
  previewImgTap(currenturl){
    const curUrl=this.$base.url+currenturl;
    const list=this.swiperList.map(v=>{
      return this.$base.url+v
    })
    uni.previewImage({
      urls:list, // 需要预览的图片链接列表
      current: curUrl// 设置当前预览的图片
    });
  },

// 支付
onPayTap(){
let that = this
if(!this.user){
this.$utils.msg("请先登录")
setTimeout(()=>{
that.$utils.jump('../login/login')
},1500)
return false
}
if(this.detail.sfsh!='是') {
this.$utils.msg("请审核通过后再操作");
return
}
uni.setStorageSync('paytable','shangjia');
uni.setStorageSync('payObject',this.detail);
this.$utils.jump('../pay-confirm/pay-confirm?type=1')
},
onDetailTap(item) {
uni.setStorageSync("useridTag",this.userid);
this.$utils.jump(`./detail?id=${item.id}&userid=`+this.userid)
},
// 跨表
async onAcrossTap(tableName,crossOptAudit,crossOptPay,statusColumnName,tips,statusColumnValue,type=1){
let that = this
if(!this.user){
this.$utils.msg("请先登录")
setTimeout(()=>{
that.$utils.jump('../login/login')
},1500)
return false
}
if(crossOptAudit=='是'&&this.detail.sfsh!='是') {
this.$utils.msg("请审核通过后再操作");
return
}
uni.setStorageSync('crossTable','shangjia');
uni.setStorageSync(`crossObj`, this.detail);
uni.setStorageSync(`statusColumnName`, statusColumnName);
uni.setStorageSync(`statusColumnValue`, statusColumnValue);
uni.setStorageSync(`tips`, tips);
if(statusColumnName!=''&&!statusColumnName.startsWith("[")) {
var obj = uni.getStorageSync('crossObj');
for (var o in obj){
if(o==statusColumnName && obj[o]==statusColumnValue){
this.$utils.msg(tips);
return
}
}
}
this.$utils.jump(`../${tableName}/add-or-update?cross=true`);
},
// 获取详情
async init(){
if(this.timer) {
clearInterval(this.timer);
}
let res = await this.$api.info('shangjia', this.id);
this.detail = res.data;

this.title = this.detail.touxiang
// 轮播图片
this.swiperList = this.detail.touxiang ? this.detail.touxiang.split(",") : [];
},
// mescroll组件初始化的回调,可获取到mescroll对象
mescrollInit(mescroll) {
this.mescroll = mescroll;
},
/*下拉刷新的回调 */
downCallback(mescroll) {
this.hasNext = true
mescroll.resetUpScroll()
},
/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
async upCallback(mescroll) {
mescroll.endSuccess(mescroll.size, this.hasNext);

},


onChatTap() {
this.$utils.jump('../chat/chat')
},
// 下载
download(url){
let _this = this;
url=_this.$base.url +  url;
uni.downloadFile({
url: url,
success: (res) => {
if (res.statusCode === 200) {
_this.$utils.msg('下载成功');
//  #ifndef  MP-WEIXIN
window.open(url);
// #endif
//  #ifdef  MP-WEIXIN
  uni.openDocument({
    filePath: res.tempFilePath,
    showMenu: true,
    success: function (res) {
      console.log('打开文档成功')
    }
  })
  console.log('点击查看文件', res.tempFilePath);
// #endif
}
}
});
},
//
onCartTabTap() {
this.$utils.tab('../shop-cart/shop-cart')
},
// 添加评论
async onCommentTap() {
if(!this.user){

return false
}
this.$utils.jump(`../discussshangjia/add-or-update?refid=${this.id}`)
},
onSHTap() {
if(!this.user){

return false
}
this.$refs.popup.open()
},
// 完成审核
async onFinishTap() {
if(!this.detail.sfsh){
this.$utils.msg('请选择审核状态');
return
}
if(!this.detail.shhf){
this.$utils.msg('请填写审核回复');
return
}
if(this.detail.sfsh=="通过"){
this.detail.sfsh = '是'
}
if(this.detail.sfsh=="不通过"){
this.detail.sfsh = '否'
}
if(this.detail.sfsh=="待审核"){
this.detail.sfsh = '待审核'
}
await this.$api.update(`shangjia`, this.detail);
this.$utils.msg('审核成功');
this.$refs.popup.close();
},
// 关闭窗口
onCloseWinTap() {
this.$refs.popup.close()
},
sfshChange(e){
console.log(this.detail)
this.sfshIndex = e.target.value
this.detail.sfsh = this.sfshOptions[this.sfshIndex]
},
}
}
</script>

